@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro');
body {
  background: #F8FDFF;
  font-family: 'Source Sans pro', 'sans-serif'; }

main {
  width: 80%;
  margin: auto;

 }  //Code for header
.header h4 {
  color: #005FFF;
  font-size: 2em; }

header {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  justify-content: space-between; }

.header ul {
  display: flex;
  justify-self: center; }

ul li {
  list-style-type: none;
  cursor: pointer; }

.header ul {
  align-items: center;
  text-transform: capitalize; }

.navi li {
  margin-left: 20px;
  color: #616263; }

.navi li:hover {
  color: #00E05A; }

.data li {
  margin-left: 30px; }

.data li:first-child {
  color: #005FFF; }

.data li:last-child {
  background: #005FFF;
  color: #fff;
  padding: 10px 20px;
  border-radius: 30px;


 }  //Code for hero content
.hero {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-column-gap: 40px;
  margin-bottom: 150px;
  margin-top: 80px;

  .left {
    margin-top: 70px;
    text-transform: capitalize;

    * {
      margin-bottom: 30px; }

    h3 {
      font-weight: 400;
      color: #868788;
      font-size: 1em; }

    a {
      text-decoration: none;
      background: #00E05A;
      color: #fff;
      padding: 15px 35px;
      display: inline-block;
      border-radius: 50px;
      box-shadow: 0px 0px 29px 0px rgba(207,205,207,1); } }

  .right img {
    width: 100%; } }

figure {
  display: grid;
  grid-template-columns: 400px 400px;
  grid-column-gap: 100px;
  justify-content: center;

  .videos {
    width: 400px;
    height: 250px;
    position: relative;
    border-radius: 0 25px 0 25px;
    box-shadow: 0px 0px 29px 0px rgba(207,205,207,1);

    img {
      width: 100%;
      height: 100%;
      border-radius: inherit; }
    i {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      background: rgba(0,0,0,0.5);
      padding: 20px;
      color: #ffffff;
      border-radius: 30px; }

    .figure {
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.5);
      border-radius: inherit; } }

  .details {
    p {
      color: #868788; }
    a {
      text-decoration: none;
      color: #00E05A;
      position: relative;
      display: block;
      font-size: .8em;

      &:before {
        position: absolute;
        width: 70px;
        height: 2px;
        background: #00E05A;
        content: '';
        top: 50%;
        left: 25%; } } } }

.product_completion ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 40px;
  margin-bottom: 40px;

  li {
    text-align: center;
    h1 {
      line-height: 50%;
      color: #005FFF; }
    p {
      color: #868788;
      font-size: 1em; } } }

.service {
  background: #f4faff;
  margin-top: 50px;
  margin-bottom: 50px;
  padding-top: 50px; }

.serviceBox {
  width: 80%;
  margin: auto; }

.serviceHead {
  text-align: center;

  hr {
    width: 5%;
    border: 2px solid #00E05A; } }

.services {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 40px;
  margin-top: 30px; }

.servicesList {
  text-align: center;
  background: #ffffff;
  padding: 70px 40px;

  > i {
    font-size: 5em;
    color: #005FFF; }
  h4 {
    font-size: 1.5em;
    font-weight: 400; }
  a {
    i {
      font-size: 2em; } } }
